import React from "react"

export default function (props) {
  const {header = null, quote = null, sourceTitle = null, title = null, children} = props;

  return (
    <article>
      <h4>{header}</h4>
      <hr />
      {children}
      <blockquote className="blockquote text-right">
        <footer className="blockquote-footer">
          {quote && <span>"{quote}" - </span>}
          <cite title={title}>{sourceTitle}</cite>
        </footer>
      </blockquote>
    </article>
  )
}