import React from "react"
import { 
  Container,
  Row,
  Col
} from "reactstrap";

import OrderList from "./layout/OrderList";

export default function (props) {
  return (
    <Container className="py-5">
      <h1 className="mb-4">Nội quy Câu lạc bộ</h1>

      <article className="mx-3 mb-4">
        <h2>A. Nội quy ngắn gọn</h2>
        <hr />
        <ol>
          <li>Thân thiện, biết chia sẻ và giúp đỡ thành viên khác.</li>
          <li>Tử tế và nhiệt tình.</li>
          <li>Yêu thương và tôn trọng thành viên khác như người thân của mình, vì Hugo là một mái nhà.</li>
        </ol>
      </article>

      <article className="mx-3">
        <h2>B. Nội quy</h2>
        <hr />
        <h3>Chú ý:</h3>
        <OrderList isOrderList={true}>
          <li>Ban chủ nhiệm Hugo mong muốn tạo ra một môi trường công bằng, văn minh. Do đó bất kể ý kiến của bạn đúng hay sai, nhưng nếu bạn vi phạm những quy tắc ứng xử, nội quy CLB thì bạn sẽ bị xử lý.</li>
          <li>Nội quy chỉ mang tính chất tương đối, tùy trường hợp sẽ có cách xử lý riêng.</li>
          <li>Hành vi không có trong luật nhưng ảnh hưởng xấu tới trong clb cũng có thể bị xử lý.</li>
        </OrderList>
        <div className="ml-3">
          <OrderList header="I. Trong hoạt động Câu lạc bộ:">
            <p className="">Các hoạt động của câu lạc bộ đều do các bạn Hugo-ers đứng ra tổ chức và phi lợi nhuận, dựa trên tình nguyện. Để tôn trọng công sức, cũng như đảm bảo chất lượng các buổi sinh hoạt, nội quy hoạt động câu lạc bộ như sau:</p>
            <ol>
              <li>
                <strong>Luôn trả lời email và thông báo sinh hoạt từ câu lạc bộ.</strong>
                <p>Nếu không thể tham gia sinh hoạt, hoặc đến muộn hãy báo lại cho leader, người quản lý nhân sự trước buổi sinh hoạt.</p>
                <p>(Văn hoá trả lời email là một thứ rất cần thiết cho các bạn sau này bao gồm cả việc đi học, cho đến đi làm. Sinh viên Việt Nam chưa có thói quen trả lời mail, và đây là thiệt thòi rất lớn)</p>
              </li>
              <li>
                <strong>Đến đúng giờ.</strong>
                <p>Học Tiếng Anh cũng là cách bạn làm quen với văn hóa đúng giờ. Đến trễ khiến bạn bỏ lỡ nhiều phần của hoạt động câu lạc bộ cũng như ảnh hưởng đến người khác, thiếu tôn trọng đến người tổ chức chương trình.</p>
              </li>
              <li>
                <strong>Có văn hoá trong các buổi sinh hoạt.</strong>
                <p>Không sử dụng bad words hoặc dirty words, không công kích, lăng mạ, nói xấu người khác trong các buổi thảo luận, sinh hoạt. Các trường hợp vi phạm sẽ bị bọn mình cảnh cáo và xử lý.
                  <i>(Tất nhiên các bạn có thể thoải mái khi nói với người quen, cực kì thân thiết của mình nhưng đảm bảo không ảnh hưởng người khác)</i>
                </p>
              </li>
            </ol>
          </OrderList>
          <OrderList header="II. Trong hoạt động của team:">
            <p className="">Đối với từng team sẽ có nội quy hoạt động khác nhau. Các bạn thành viên nhớ chú ý theo dõi các thông tin do leader, người phụ trách team đăng tải.</p>
            <ul className="list-unstyled">
              <li className="mb-3 ml-4">
                <strong>Thời gian sinh hoạt:</strong> tuỳ thuộc vào team bạn đang tham gia.
                <ol>
                  <li>Cố định: <em>Power Rangers</em> (Thứ 5), <em>Heroes Company</em> (thứ 6), <em>Banana</em> (Thứ 7), <em>Niffler</em> (Chủ nhật).</li>
                  <li>Việc thay đổi lịch sinh hoạt, giờ giấc được thông báo qua leader, người phụ trách team và trên group các team</li>
                </ol>
              </li>
              <li className="ml-4"><strong>Địa điểm sinh hoạt:</strong> tuỳ thuộc vào team. Được thông báo bởi leader, người phụ trách team và trên group các team hàng tuần.</li>
            </ul>
          </OrderList>
          <OrderList header="III. Hoạt động trong group:">
            <p className="">Về group Hugo, bọn mình có bài viết đăng tải riêng <a href="https://www.facebook.com/HUGOClub">tại đây</a>.
            </p>
          </OrderList>
        </div>
      </article>
    </Container>
  )
}