import React, {Component} from "react";
import {
  BrowserRouter as Router,
  Route,
} from "react-router-dom";

import "bootstrap/dist/css/bootstrap.min.css"
import "./public/css/index.css"

import TopNav from "./components/TopNav/TopNav"
import Home from "./components/Home"
import NoiQuyCauLacBo from "./components/NoiQuyCauLacBo"
import NoiQuyGroup from "./components/NoiQuyGroup";
import FAQ from "./components/FAQ"


class App extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Router>
        <TopNav />
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/NoiQuyCauLacBo" exact>
          <NoiQuyCauLacBo />
        </Route>
        <Route path="/NoiQuyGroup" exact>
          <NoiQuyGroup />
        </Route>
        <Route path="/FAQ" exact>
          <FAQ />
        </Route>
      </Router>
    )
  }
}

export default App;