import React, { useState } from "react";
import classNames from "classnames"
import {
  Link
} from "react-router-dom";

import { 
  Col,
  Nav,
  NavLink 
} from "reactstrap";

export default function (props) {
  const [isActive, setIsActive] = useState({
    home: true,
    rule: false,
    ruleOfGroup: false,
    FAQ: false
  });

  function onClickHandle() {
    return (event) => {
      const state = {
        home: false,
        ruleOfClub: false,
        ruleOfGroup: false,
        FAQ: false
      }

      const { id } = event.target;
      state[id] = true;

      setIsActive(state)
    }
  }

  const className = "nav-link text-reset";

  return (
    <Col xs={"auto"}>
      <Nav className="text-color">
        <Link id="home" className={classNames(className, {active: isActive.home})} to={`/`} onClick={onClickHandle()}>TRANG CHỦ</Link>
        <Link id="ruleOfClub" className={classNames(className, {active: isActive.ruleOfClub})} to={`/NoiQuyCauLacBo`} onClick={onClickHandle()}>NỘI QUY CÂU LẠC BỘ</Link>
        <Link id="ruleOfGroup" className={classNames(className, {active: isActive.ruleOfGroup})} to={`/NoiQuyGroup`} onClick={onClickHandle()}>NỘI QUY GROUP</Link>
        <Link id="FAQ" className={classNames(className, {active: isActive.FAQ})} to={`/FAQ`} onClick={onClickHandle()}>CÁC CÂU HỎI THƯỜNG GẶP</Link>
      </Nav>
    </Col>
  )
}