import React from 'react';

import {
  Container,
  Row,
  Col,
  Navbar,
  NavbarBrand
} from 'reactstrap';

import logo from "../../public/img/logo.png"
import NavItems from "./NavItems";

const TopNav = (props) => {
  return (
    <Navbar className="navbar-dark bg-dark pb-0 pt-4 flex-column">
      <Container className="row justify-content-end">
        <Col xs={"auto"}>
          <NavbarBrand href="/">
            <Row>
              <Col>
                <h4 className="text-right">Hugo English Club</h4>
                <p className="lead text-right m-0">Light up your fire.</p>
              </Col>
              <Col>
                <img className="bg-light rounded-circle" src={logo} height="75px"/>
              </Col>
            </Row>
          </NavbarBrand>
        </Col>
      </Container>

      <Container className="row">
        <NavItems />
      </Container>
    </Navbar>
  );
}

export default TopNav;