import React from "react";
import { 
  Container,
  Row,
  Col
} from "reactstrap";

export default function (props) {
  return <Container className="py-5">
    <h1 className="mb-4">Nội quy Group</h1>
    <p>Group Hugo là nơi sinh hoạt chung của thành viên câu lạc bộ Hugo từ tất cả các thế hệ, là nơi <strong>chia sẻ</strong>, <strong>giao lưu</strong> kiến thức Tiếng Anh và <strong>thảo luận</strong> mọi vấn đề trong cuộc sống. </p>
    <article className="mx-3 mb-4">
      <h2>A. Các bài viết thông báo (hashtag #announcement)</h2>
      <hr />
      <ol>
        <li>Các bài thông báo thường do admin của Hugo Club đăng tải, hoặc các quản trị viên là thành viên ban chủ nhiệm.</li>
        <li>Các bài viết bao gồm thông báo sinh hoạt CLB, thời gian, địa điểm cũng như các thông tin cần thiết cho buổi sinh hoạt.</li>
        <li>Thông tin Event, ngoại khoá.</li>
        <li>Các bài viết khác có tính chất quan trọng, đặc biệt.</li>
      </ol>
    </article>

    <article className="mx-3 mb-4">
      <h2>B. Bài viết của thành viên</h2>
      <hr />
      <ol>
        <li><strong>Ngôn ngữ sử dụng:</strong> Tiếng Việt, Tiếng Anh.</li>
        <li><strong>Phải có hashtag</strong> để phân loại nội dung bài viết, giúp mọi người dễ dàng tìm kiếm bài theo chủ đề.</li>
        <li><strong>KHÔNG đăng bài viết quảng cáo (rao vặt, livestream, ...)</strong> Bài viết tuyển dụng các công việc liên quan tới ngành nghề CNTT được chấp nhận.</li>
        <li><strong>Hãy tìm hiểu trước qua Google</strong> để tránh hỏi những vấn đề đã có sẵn câu trả lời. Hỏi phải nói có đầu có đuôi, nói rõ vấn đề vướng mắc, <strong>KHÔNG hỏi trống lóc</strong>.</li>
        <li><strong>Dùng chức năng tìm kiếm bài viết</strong> trong nhóm trước khi đăng để tránh việc trùng lặp nội dung.</li>
        <li><strong>KHÔNG sử dụng các liên kết rút gọn kiếm tiền hoặc liên kết tiếp thị (affiliate)</strong> trong bài viết với mục đích kiếm lợi nhuận từ nhóm.</li>
        <li><strong>Có văn hoá ứng xử:</strong> Mặc định tất cả các bài viết được coi là nghiêm túc. Khi tranh luận <strong>PHẢI</strong> sử dụng lý lẽ, luận điểm, logic. Không comment miệt thị đối phương, phân biệt giới tính, vùng miền. Không chửi tục, bình luận không liên quan đến chủ đề bài viết.</li>
      </ol>
    </article>

    <article className="mx-3 mb-4">
      <h2>C. Hashtag được cho phép</h2>
      <hr />
      <dl className="row">
        <dt className="col-sm-2">#askHugo</dt>
        <dd className="col-sm-10 mb-4">Bạn hỏi Hugo những điều mình thắc mắc. Các câu hỏi liên quan đến ngoại ngữ hoặc câu lạc bộ sẽ được ưu tiên kiểm duyệt.<br />Ví dụ: xin tư vấn học Tiếng Anh; xin giải đáp, hỏi nghĩa.</dd>

        <dt className="col-sm-2">#askme</dt>
        <dd className="col-sm-10 mb-4">Bạn là một chuyên gia, một cá nhân với nhiều kinh nghiệm trong lĩnh vực nào đó, hoặc bạn thấy mình có thể giúp đỡ người khác. Với các bài đăng <strong>AskMe</strong>, bạn sẽ mô tả thông tin của mình trong đó và lĩnh vực bạn có thể trả lời. Các thành viên quan tâm sẽ hỏi ở phần comment.</dd>

        <dt className="col-sm-2">#sharing</dt>
        <dd className="col-sm-10 mb-4">Chia sẻ những thông tin mà bạn thấy hữu ích. Các chia sẻ liên quan đến ngoại ngữ sẽ được ưu tiên kiểm duyệt.</dd>

        <dt className="col-sm-2">#tips</dt>
        <dd className="col-sm-10 mb-4">Undefined</dd>

        <dt className="col-sm-2">#writing</dt>
        <dd className="col-sm-10 mb-4">Bài viết của bạn. Nhằm rèn luyện kĩ năng viết cho các bạn thành viên. Các bài Writing sẽ thường được kiểm duyệt và được các thành viên ban chuyên môn CLB, cũng như thành viên góp ý, sửa lỗi. Chủ đề các bài viết Writing không bị giới hạn, và các bạn có thể nhận được quyền lợi thông qua những bài viết của mình.<br />Bài viết có thể đi kèm bản dịch, hoặc được thành viên khác dịch.</dd>

        <dt className="col-sm-2">#translate</dt>
        <dd className="col-sm-10 mb-4">Các bài dịch thuật từ các trang thông tin như Reddit, Quora, Medium, hoặc các bài báo. Đối với các bài viết tự dịch, lần đầu đăng tải tại group Hugo sẽ nhận được nhiều quyền lợi. Các bài Reup từ nguồn khác các bạn phải <strong>đảm bảo</strong> có được sự cho phép từ tác giả.</dd>

        <dt className="col-sm-2">#photo</dt>
        <dd className="col-sm-10 mb-4">Các bài viết chia sẻ hình ảnh hoạt động, con người ở Hugo.</dd>

        <dt className="col-sm-2">#funny</dt>
        <dd className="col-sm-10 mb-4">Các bài viết giải trí liên quan Tiếng Anh hoặc lĩnh vực khác.</dd>

        <dt className="col-sm-2">#voiceout</dt>
        <dd className="col-sm-10 mb-4">Chia sẻ của bản thân bạn về bất cứ gì, từ chuyện tình cảm, học hành đến sự nghiệp và bạn muốn ai đó lắng nghe bạn.<br />Ngoài ra bao gồm những bài viết của thành viên thể hiện quan điểm, chia sẻ của bản thân đến câu lạc bộ.</dd>

        <dt className="col-sm-2">#news</dt>
        <dd className="col-sm-10 mb-4">Các bài viết liên quan đến thông tin thế giới, VN ở dạng Tiếng Anh.</dd>

        <dt className="col-sm-2">#review</dt>
        <dd className="col-sm-10 mb-4">Các bài viết đánh giá sách, phim ảnh, ...</dd>

        <dt className="col-sm-2">#opportunity</dt>
        <dd className="col-sm-10 mb-4">Đăng tải các thông tin về cơ hội tình nguyện viên, du học và việc làm liên quan đến sử dụng ngoại ngữ.</dd>
      </dl>
    </article>

    <article className="mx-3 mb-4">
      <h2>D. Quyền lợi</h2>
      <hr />
      <ul>
        <li>Các thành viên hoạt động năng nổ, có nhiều bài viết sẽ được tổng hợp và thông báo vào cuối tháng và được ghi nhận bởi CLB.</li>
        <li>Các bài viết có chất lượng, được ủng hộ sẽ được đăng tải trên fanpage kèm credit của thành viên đó. Bài viết được đăng tải có <strong>sự chấp thuận</strong> của thành viên đó.</li>
        <li>Các bài dịch thuật, writing chất lượng, nguyên bản sẽ được CLB xem xét và đăng ký bản quyền bài viết với thành viên đó, nhằm <strong>đảm bảo quyền lợi</strong> cũng như <strong>tôn trọng công sức</strong> của thành viên đã đóng góp đến CLB.</li>
      </ul>
    </article>
  </Container>
}