import React from "react";

export default function (props) {
  const {header = null, children = null, isOrderList = false} = props;

  function render() {
    return <ul style={{
      listStyleType: "none"
    }}>
      {header ? <strong>{header}</strong> : null}
      <div className="ml-4">
        {children}
      </div>
    </ul>
  }

  return (
    render()
  )
}