import React from 'react'

import { 
  Container,
  Col,
  Row
} from "reactstrap";

export default function (props) {
  return (
    <Container className="py-5">
      <h1 className="mb-4">CLB Tiếng Anh Hugo</h1>
      <p>CLB Tiếng Anh Hugo là một trong hơn 15 CLB chính thống trực thuộc Hội Sinh viên Đại học Bách Khoa - Đại học Đà Nẵng, được thành lập vào 15  tháng 3 năm 2008 bởi các sinh viên Trường đại học Bách Khoa.</p>
      <p>Mục tiêu  của CLB là xây dựng môi trường năng động động cho mọi sinh viên có mong muốn rèn luyện Tiếng Anh, nâng cao những kĩ năng mới và trải nghiệm những hoạt động mới mẻ.</p>
      <blockquote className="blockquote text-right">
        <footer className="blockquote-footer">
          <cite className="ml-2" title="Hugo English Club - Light up your fire">Slogan - Light up your fire</cite>
        </footer>
      </blockquote>

      <article className="mx-3 mb-4">
        <h2>Thời gian hoạt động</h2>
        <hr />
        <ul>
          <li className="mb-3"><strong>Thời gian sinh hoạt:</strong> <span className="text-danger">8:00 Sáng Chủ Nhật hàng tuần.</span></li>
          <li><strong>Địa điểm sinh hoạt:</strong> <span className="text-danger">Sân tự học khu C, trước phòng C116, Đại học Bách Khoa Đà Nẵng</span></li>
          <p>Việc thay đổi địa điểm sinh hoạt do tổ chức sự kiện, ngoại khóa sẽ được thông báo qua email hoặc trên group Hugo. Do đó, các bạn <strong>nhớ kiểm tra email</strong>, <strong>thông báo</strong> trên group trước mỗi buổi sinh hoạt.</p>
          <li>
            <strong>Hình thức nhận thông báo:</strong>
            <ol>
              <li>Các bạn sẽ nhận được thông báo sinh hoạt hàng tuần từ email CLB Hugo.</li>
              <li>Địa chỉ email clb: <em>hugoclub.dut@gmail.com</em></li>
              <li>Ngoài ra, thông báo cũng được post trên group Hugo, kèm một số thông tin cần thiết cho buổi sinh hoạt đó.</li>
            </ol>
          </li>
        </ul>
      </article>

      <article className="mx-3 mb-4">
        <h2>Các hình thức hoạt động của CLB</h2>
        <hr />
        <dl className="row">
          <dt className="col-sm-2">Weekly Meeting</dt>
          <dd className="col-sm-10 mb-4">8 giờ sáng Chủ nhật hàng tuần với các topic và format thay đổi liên tục. Trong weekly meeting các bạn sẽ được tham gia các hoạt động như Thuyết trình (Presentation), Diễn kịch (Acting), Thảo luận (Discussion) cùng với những trò chơi vô cùng thú vị và bổ ích.</dd>

          <dt className="col-sm-2">Team Meeting</dt>
          <dd className="col-sm-10 mb-4">Sinh hoạt hàng tuần ở các team. Trong các buổi sinh hoạt team thành viên có cơ hội được chia sẻ nhiều hơn về bản thân, cũng như có nhiều hoạt động do thành viên, leader của team tổ chức.</dd>

          <dt className="col-sm-2">Team Building Event</dt>
          <dd className="col-sm-10 mb-4">Các sự kiện ngoại khoá được tổ chức ở công viên, bãi biển nhằm kết hợp Tiếng Anh cũng như rèn luyện kỹ năng teamwork của thành viên.</dd>

          <dt className="col-sm-2">Collaboration</dt>
          <dd className="col-sm-10 mb-4">Hoạt động liên kết với các câu lạc bộ khác trên địa bàn thành phố Đà Nẵng</dd>

          <dt className="col-sm-2">English Contest</dt>
          <dd className="col-sm-10 mb-4">Các thành viên có cơ hội góp mặt vào danh sách đội tuyển tham gia Olympic Tiếng Anh toàn quốc diễn ra tháng 10 hàng năm. Ngoài ra, Hugo còn tổ chức cuộc thi Tiếng Anh vào tháng 3 ~ tháng 4 nhằm tuyển chọn đội tuyển <em>Olympiad</em>.</dd>

          <dt className="col-sm-2">Camping</dt>
          <dd className="col-sm-10 mb-4">Thành viên có cơ hội tham gia hoạt động cắm trại của CLB và team. Trong đó hoạt động cắm trại CLB là sự kiện lớn nhất mùa đông hàng năm.</dd>

          <dt className="col-sm-2">Cultural Exchange</dt>
          <dd className="col-sm-10 mb-4">Hoạt động giao lưu với người nước ngoài.</dd>

          <dt className="col-sm-2">Soft-Skill Contest</dt>
          <dd className="col-sm-10 mb-4">Đây thường là sự kiện liên kết với các câu lạc bộ khác, nhằm tạo ra sân chơi rèn luyện kỹ năng mềm, cũng như Tiếng Anh.</dd>

          <dt className="col-sm-2">Boardgame</dt>
          <dd className="col-sm-10 mb-4">Hoạt động chơi boardgame giải trí ...</dd>

          <dt className="col-sm-2">Group</dt>
          <dd className="col-sm-10 mb-4">Các hoạt động được tổ chức trên group chính thức của CLB Hugo.</dd>
        </dl>
      </article>
    </Container>
  )
}