import React from "react"
import { Container } from "reactstrap"

import Article from "./layout/Article"

export default function (props) {
  return <Container className="py-5">
    <h1 className="mb-4">Các câu hỏi thường gặp</h1>

    <Article className="mx-3 mb-4"
      header="1. Hugo là gì?"
      quote="Logo của CLB thể hiện hình ảnh chú lùn Hugo"
      sourceTitle="Dũng"
      title="President"
    >
      <h6>Hugo là một nhân vật nổi tiếng trên truyền hình ở Việt Nam trong thập niên 2000</h6>
      <p>Những năm 2004, 2005 là thời điểm mà các gameshow dành cho thanh thiếu niên bắt đầu nở rộ tại Việt Nam. Một trong những cái tên đình đám nhất lúc bấy giờ và vẫn để lại dư âm cho đến tận hôm nay chính là "Vui cùng Hugo". “Vui cùng Hugo" là một trong những gameshow mà người chơi chỉ cần ngồi tại nhà là đã có thể tham gia được với chương trình. Hình ảnh chú lùn Hugo đã đi vào ký ức của nhiều 8X, 9X. </p>
      <p>Hình tượng Hugo thể hiện cho văn hoá đại chúng giới trẻ thời bấy giờ. Do đó, những sinh viên sáng lập nên câu lạc bộ Hugo đã chọn hình tượng chú lùn này cho CLB, để thể hiện sự năng động, khôn khéo, dũng cảm và ham học hỏi của thành viên CLB, giống như hình tượng Hugo vậy. </p>

    </Article>

    <Article className="mx-3 mb-4"
      header="2. Làm thế nào để tham gia CLB Tiếng Anh Hugo?"
      sourceTitle="Dũng"
      title="President"
    >
      <p>
        Nếu bạn đang có mong muốn gia nhập CLB, hoặc muốn giới thiệu đến người thân, bạn bè của mình, đọc kỹ những thông tin bên dưới nhé.
      </p>

      <ul>
        <li className="mb-3">
          <strong>Tuyển thành viên:</strong>
          &nbsp;Mỗi năm CLB có hai mùa tuyển thành viên, mùa xuân  (tháng 2 ~ tháng 3)  và mùa thu (tháng 8 ~ tháng 9)
          <ol>
            <li>Hình thức tuyển: Vòng đơn Online - Vòng Phỏng vấn trực tiếp</li>
            <li>Số lượng tuyển: 30~40 thành viên cho mỗi đợt</li>
          </ol>
        </li>
        <li>
          <strong>Sau khi trở thành thành viên CLB:</strong>
          &nbsp;Bạn sẽ được tham gia group Hugo English Club, có các đặc quyền và cơ hội trải nghiệm nhiều hoạt động của CLB, cùng với đó là gia nhập một trong bốn team (Power Rangers, Heroes Company, Banana và Niffler).
        </li>
      </ul>
    </Article>

    <Article className="mx-3 mb-4"
      header="3. Quyền lợi của thành viên CLB Hugo:"
      sourceTitle="Dũng"
      title="President"
    >
      <ol>
        <li>Có môi trường rèn luyện Tiếng Anh giao tiếp. Được chỉ dẫn bởi nhiều thành viên, cựu thành viên trong CLB.</li>
        <li>Làm quen được nhiều bạn bè từ khắp nơi đang học tập, làm việc và sinh sống trên địa bàn thành phố Đà Nẵng. Thành viên CLB bao gồm từ học sinh THPT, sinh viên Đại học cho đến người đã đi làm.</li>
        <li>Có đặc quyền, cơ hội tham gia các hoạt động do CLB Tiếng Anh Hugo tổ chức, cũng như các hoạt động liên kết khác.</li>
        <li>Được tiếp cận các nguồn thông tin về Tình nguyện viên, Du học, và các công việc liên quan đến sử dụng Ngoại ngữ.</li>
        <li>Được ưu tiên tham gia các hoạt động, đặc biệt có cấp giấy chứng nhận, chứng chỉ.</li>
        <li>Tham gia các hoạt động giao lưu văn hoá với người nước ngoài.</li>
      </ol>
    </Article>

    <Article className="mx-3 mb-4"
      header="4. Thông tin Ban chủ nhiệm:"
      sourceTitle="Dũng"
      title="President"
    >
      <ul>
        <li>Chủ nhiệm CLB: Phạm Mạnh Dũng.</li>
        <li>Trưởng ban Nhân sự: Phan Đức Phú Cường</li>
        <li>Trưởng ban sự kiện: Trần Quốc Tuấn</li>
        <li>Trưởng ban Truyền thông: Nguyễn Lê Anh Minh</li>
        <li>Trưởng ban Chuyên môn: Lê Nữ Minh Huệ</li>
      </ul>
    </Article>
  </Container>
}